AAA,CAGJ,mBACI,gBAAA,CAGJ,eACI,gBAAA,CAAA,CAIR,yBACI,UACI,cAAA,CACA,UAAA,CACA,QAAA,CACA,qBAAA,CACA,qBAAA,CACA,UAAA,CACA,iBAAA,CACA,cAAA,CACA,sCAAA,CACA,cAAA,CAGJ,iBACI,MAAA,CAGJ,WACI,YAAA,CAGJ,oCACI,SAAA,CAGJ,oCACI,uCAAA,CAGJ,oCACI,yCAAA,CAGJ,mBACI,cAAA,CACA,gBAAA,CAGJ,UACI,gBAAA,CACA,eAAA,CAGJ,mBACI,gBAAA,CAGJ,mBACI,cAAA,CAGJ,eACI,cAAA,CAGJ,iBACI,yBAAA,CACA,QAAA,CAGJ,wBACI,YAAA,CAGJ,cACI,WAAA,CACA,UAAA,CACA,UAAA,CACA,WAAA,CACA,cAAA,CAAA,CAIR,yBACI,WACI,cAAA,CAGJ,eACI,cAAA,CAGJ,mBACI,cAAA,CAGJ,kBACI,cAAA,CAGJ,mBACI,gBAAA,CAGJ,kBACI,cAAA,CAGJ,eACI,gBAAA,CAGJ,cACI,cAAA,CAGJ,cACI,yBAAA,CAGJ,gBACI,yBAAA,CACA,iBAAA,CAGJ,cACI,sBAAA,CAGJ,eACI,eAAA,CAGJ,aACI,oBAAA,CACA,eAAA,CAAA,CAIR,yBACI,mBACI,gBAAA,CAGJ,mBACI,gBAAA,CAGJ,eACI,gBAAA,CAGJ,UACI,gBAAA,CAGJ,YACI,gBAAA,CAAA,CAKR,aACI,iBAAA,CAGJ,MACI,kBAAA,CAGJ,MACI,mBAAA,CAIJ,oBACI,SAAA,CAGJ,0BACI,kBAAA,CAGJ,0BACI,kBAAA,CACA,iBAAA,CAGJ,gCACI,kBAAA","sourcesContent":["/* Reset y estilos base */\r\n* {\r\n    margin: 0;\r\n    padding: 0;\r\n    box-sizing: border-box;\r\n}\r\n\r\nhtml {\r\n    scroll-behavior: smooth;\r\n}\r\n\r\nbody {\r\n    font-family: 'Poppins', sans-serif;\r\n    line-height: 1.6;\r\n    color: #333;\r\n    overflow-x: hidden;\r\n}\r\n\r\n.container {\r\n    max-width: 1200px;\r\n    margin: 0 auto;\r\n    padding: 0 20px;\r\n}\r\n\r\n/* Sticky Menu */\r\n.navbar {\r\n    position: fixed;\r\n    top: 0;\r\n    width: 100%;\r\n    background: rgba(255, 255, 255, 0.95);\r\n    backdrop-filter: blur(10px);\r\n    z-index: 1000;\r\n    transition: all 0.3s ease;\r\n    box-shadow: 0 2px 20px rgba(0, 0, 0, 0.1);\r\n}\r\n\r\n.nav-container {\r\n    max-width: 1200px;\r\n    margin: 0 auto;\r\n    padding: 0 20px;\r\n    display: flex;\r\n    justify-content: space-between;\r\n    align-items: center;\r\n    height: 70px;\r\n}\r\n\r\n.nav-logo {\r\n    display: flex;\r\n    align-items: center;\r\n    font-size: 1.5rem;\r\n    font-weight: 700;\r\n    color: #2563eb;\r\n}\r\n\r\n.nav-logo i {\r\n    margin-right: 10px;\r\n    font-size: 1.8rem;\r\n}\r\n\r\n.nav-menu {\r\n    display: flex;\r\n    list-style: none;\r\n    gap: 2rem;\r\n}\r\n\r\n.nav-link {\r\n    text-decoration: none;\r\n    color: #333;\r\n    font-weight: 500;\r\n    transition: color 0.3s ease;\r\n    position: relative;\r\n}\r\n\r\n.nav-link:hover {\r\n    color: #2563eb;\r\n}\r\n\r\n.nav-link::after {\r\n    content: '';\r\n    position: absolute;\r\n    width: 0;\r\n    height: 2px;\r\n    bottom: -5px;\r\n    left: 0;\r\n    background-color: #2563eb;\r\n    transition: width 0.3s ease;\r\n}\r\n\r\n.nav-link:hover::after {\r\n    width: 100%;\r\n}\r\n\r\n/* Language Selector */\r\n.language-selector {\r\n    position: relative;\r\n    margin-right: 1rem;\r\n}\r\n\r\n.lang-btn {\r\n    display: flex;\r\n    align-items: center;\r\n    gap: 0.5rem;\r\n    background: transparent;\r\n    border: 2px solid #e5e7eb;\r\n    border-radius: 25px;\r\n    padding: 8px 16px;\r\n    cursor: pointer;\r\n    transition: all 0.3s ease;\r\n    font-size: 0.9rem;\r\n    font-weight: 500;\r\n}\r\n\r\n.lang-btn:hover {\r\n    border-color: #2563eb;\r\n    background: rgba(37, 99, 235, 0.1);\r\n}\r\n\r\n.lang-btn i {\r\n    font-size: 1rem;\r\n    color: #2563eb;\r\n}\r\n\r\n.lang-dropdown {\r\n    position: absolute;\r\n    top: 100%;\r\n    right: 0;\r\n    background: white;\r\n    border-radius: 10px;\r\n    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);\r\n    padding: 0.5rem 0;\r\n    min-width: 150px;\r\n    opacity: 0;\r\n    visibility: hidden;\r\n    transform: translateY(-10px);\r\n    transition: all 0.3s ease;\r\n    z-index: 1001;\r\n}\r\n\r\n.lang-dropdown.show {\r\n    opacity: 1;\r\n    visibility: visible;\r\n    transform: translateY(0);\r\n}\r\n\r\n.lang-option {\r\n    display: block;\r\n    padding: 0.75rem 1rem;\r\n    text-decoration: none;\r\n    color: #333;\r\n    transition: background 0.3s ease;\r\n    font-size: 0.9rem;\r\n}\r\n\r\n.lang-option:hover {\r\n    background: #f8fafc;\r\n    color: #2563eb;\r\n}\r\n\r\n.lang-option.active {\r\n    background: #2563eb;\r\n    color: white;\r\n}\r\n\r\n.hamburger {\r\n    display: none;\r\n    flex-direction: column;\r\n    cursor: pointer;\r\n}\r\n\r\n.bar {\r\n    width: 25px;\r\n    height: 3px;\r\n    background-color: #333;\r\n    margin: 3px 0;\r\n    transition: 0.3s;\r\n}\r\n\r\n/* Header */\r\n.header {\r\n    height: 100vh;\r\n    background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: center;\r\n    text-align: center;\r\n    color: white;\r\n    position: relative;\r\n    overflow: hidden;\r\n}\r\n\r\n.header::before {\r\n    content: '';\r\n    position: absolute;\r\n    top: 0;\r\n    left: 0;\r\n    right: 0;\r\n    bottom: 0;\r\n    background: url('data:image/svg+xml,<svg xmlns=\"http://www.w3.org/2000/svg\" viewBox=\"0 0 100 100\"><defs><pattern id=\"grain\" width=\"100\" height=\"100\" patternUnits=\"userSpaceOnUse\"><circle cx=\"50\" cy=\"50\" r=\"1\" fill=\"white\" opacity=\"0.1\"/></pattern></defs><rect width=\"100\" height=\"100\" fill=\"url(%23grain)\"/></svg>');\r\n    opacity: 0.3;\r\n}\r\n\r\n.header-content {\r\n    position: relative;\r\n    z-index: 2;\r\n}\r\n\r\n.header-content h1 {\r\n    font-size: 3.5rem;\r\n    font-weight: 700;\r\n    margin-bottom: 1rem;\r\n    animation: fadeInUp 1s ease;\r\n}\r\n\r\n.header-content p {\r\n    font-size: 1.2rem;\r\n    margin-bottom: 2rem;\r\n    opacity: 0.9;\r\n    animation: fadeInUp 1s ease 0.2s both;\r\n}\r\n\r\n.cta-button {\r\n    display: inline-block;\r\n    padding: 15px 30px;\r\n    background: #2563eb;\r\n    color: white;\r\n    text-decoration: none;\r\n    border-radius: 50px;\r\n    font-weight: 600;\r\n    transition: all 0.3s ease;\r\n    animation: fadeInUp 1s ease 0.4s both;\r\n}\r\n\r\n.cta-button:hover {\r\n    background: #1d4ed8;\r\n    transform: translateY(-2px);\r\n    box-shadow: 0 10px 25px rgba(37, 99, 235, 0.3);\r\n}\r\n\r\n/* Rotador - Slider */\r\n.slider-section {\r\n    position: relative;\r\n    height: 400px;\r\n    overflow: hidden;\r\n    background: #f8fafc;\r\n}\r\n\r\n.slider-container {\r\n    height: 100%;\r\n    position: relative;\r\n}\r\n\r\n.slider-slide {\r\n    position: absolute;\r\n    top: 0;\r\n    left: 0;\r\n    width: 100%;\r\n    height: 100%;\r\n    opacity: 0;\r\n    transition: opacity 0.5s ease;\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: center;\r\n    background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);\r\n}\r\n\r\n.slider-slide:nth-child(2) {\r\n    background: linear-gradient(135deg, #f093fb 0%, #f5576c 100%);\r\n}\r\n\r\n.slider-slide:nth-child(3) {\r\n    background: linear-gradient(135deg, #4facfe 0%, #00f2fe 100%);\r\n}\r\n\r\n.slider-slide:nth-child(4) {\r\n    background: linear-gradient(135deg, #43e97b 0%, #38f9d7 100%);\r\n}\r\n\r\n.slider-slide.active {\r\n    opacity: 1;\r\n}\r\n\r\n.slider-content {\r\n    text-align: center;\r\n    color: white;\r\n    padding: 0 20px;\r\n}\r\n\r\n.slider-content h2 {\r\n    font-size: 2.5rem;\r\n    font-weight: 700;\r\n    margin-bottom: 1rem;\r\n}\r\n\r\n.slider-content p {\r\n    font-size: 1.1rem;\r\n    opacity: 0.9;\r\n}\r\n\r\n.slider-dots {\r\n    position: absolute;\r\n    bottom: 20px;\r\n    left: 50%;\r\n    transform: translateX(-50%);\r\n    display: flex;\r\n    gap: 10px;\r\n}\r\n\r\n.dot {\r\n    width: 12px;\r\n    height: 12px;\r\n    border-radius: 50%;\r\n    background: rgba(255, 255, 255, 0.5);\r\n    cursor: pointer;\r\n    transition: background 0.3s ease;\r\n}\r\n\r\n.dot.active {\r\n    background: white;\r\n}\r\n\r\n.slider-btn {\r\n    position: absolute;\r\n    top: 50%;\r\n    transform: translateY(-50%);\r\n    background: rgba(255, 255, 255, 0.2);\r\n    border: none;\r\n    color: white;\r\n    padding: 15px;\r\n    border-radius: 50%;\r\n    cursor: pointer;\r\n    transition: all 0.3s ease;\r\n    backdrop-filter: blur(10px);\r\n}\r\n\r\n.slider-btn:hover {\r\n    background: rgba(255, 255, 255, 0.3);\r\n}\r\n\r\n.slider-btn.prev {\r\n    left: 20px;\r\n}\r\n\r\n.slider-btn.next {\r\n    right: 20px;\r\n}\r\n\r\n/* Sección de Servicios */\r\n.services {\r\n    padding: 80px 0;\r\n    background: white;\r\n}\r\n\r\n.section-title {\r\n    text-align: center;\r\n    font-size: 2.5rem;\r\n    font-weight: 700;\r\n    margin-bottom: 3rem;\r\n    color: #1f2937;\r\n}\r\n\r\n.services-grid {\r\n    display: grid;\r\n    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));\r\n    gap: 2rem;\r\n}\r\n\r\n.service-card {\r\n    background: white;\r\n    padding: 2rem;\r\n    border-radius: 15px;\r\n    text-align: center;\r\n    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);\r\n    transition: all 0.3s ease;\r\n    border: 1px solid #e5e7eb;\r\n}\r\n\r\n.service-card:hover {\r\n    transform: translateY(-10px);\r\n    box-shadow: 0 20px 40px rgba(0, 0, 0, 0.15);\r\n}\r\n\r\n.service-icon {\r\n    width: 80px;\r\n    height: 80px;\r\n    background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);\r\n    border-radius: 50%;\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: center;\r\n    margin: 0 auto 1.5rem;\r\n}\r\n\r\n.service-icon i {\r\n    font-size: 2rem;\r\n    color: white;\r\n}\r\n\r\n.service-card h3 {\r\n    font-size: 1.3rem;\r\n    font-weight: 600;\r\n    margin-bottom: 1rem;\r\n    color: #1f2937;\r\n}\r\n\r\n.service-card p {\r\n    color: #6b7280;\r\n    line-height: 1.6;\r\n}\r\n\r\n/* Galería de Imágenes */\r\n.gallery {\r\n    padding: 80px 0;\r\n    background: #f8fafc;\r\n}\r\n\r\n.gallery-grid {\r\n    display: grid;\r\n    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));\r\n    gap: 1.5rem;\r\n}\r\n\r\n.gallery-item {\r\n    position: relative;\r\n    border-radius: 15px;\r\n    overflow: hidden;\r\n    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);\r\n    transition: all 0.3s ease;\r\n}\r\n\r\n.gallery-item:hover {\r\n    transform: scale(1.05);\r\n}\r\n\r\n.gallery-item img {\r\n    width: 100%;\r\n    height: 250px;\r\n    object-fit: cover;\r\n    transition: transform 0.3s ease;\r\n}\r\n\r\n.gallery-item:hover img {\r\n    transform: scale(1.1);\r\n}\r\n\r\n.gallery-overlay {\r\n    position: absolute;\r\n    bottom: 0;\r\n    left: 0;\r\n    right: 0;\r\n    background: linear-gradient(transparent, rgba(0, 0, 0, 0.8));\r\n    color: white;\r\n    padding: 2rem;\r\n    transform: translateY(100%);\r\n    transition: transform 0.3s ease;\r\n}\r\n\r\n.gallery-item:hover .gallery-overlay {\r\n    transform: translateY(0);\r\n}\r\n\r\n.gallery-overlay h3 {\r\n    font-size: 1.2rem;\r\n    font-weight: 600;\r\n    margin-bottom: 0.5rem;\r\n}\r\n\r\n.gallery-overlay p {\r\n    opacity: 0.9;\r\n}\r\n\r\n/* Video Section */\r\n.video-section {\r\n    padding: 80px 0;\r\n    background: white;\r\n}\r\n\r\n.video-container {\r\n    position: relative;\r\n    width: 100%;\r\n    max-width: 800px;\r\n    margin: 0 auto;\r\n    border-radius: 15px;\r\n    overflow: hidden;\r\n    box-shadow: 0 20px 40px rgba(0, 0, 0, 0.1);\r\n}\r\n\r\n.video-container iframe {\r\n    width: 100%;\r\n    height: 450px;\r\n    border: none;\r\n}\r\n\r\n/* Formulario de Contacto */\r\n.contact {\r\n    padding: 80px 0;\r\n    background: #f8fafc;\r\n}\r\n\r\n.contact-content {\r\n    display: grid;\r\n    grid-template-columns: 1fr 1fr;\r\n    gap: 3rem;\r\n    align-items: start;\r\n}\r\n\r\n.contact-info h3 {\r\n    font-size: 1.5rem;\r\n    font-weight: 600;\r\n    margin-bottom: 2rem;\r\n    color: #1f2937;\r\n}\r\n\r\n.contact-item {\r\n    display: flex;\r\n    align-items: center;\r\n    margin-bottom: 1.5rem;\r\n}\r\n\r\n.contact-item i {\r\n    width: 50px;\r\n    height: 50px;\r\n    background: #2563eb;\r\n    color: white;\r\n    border-radius: 50%;\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: center;\r\n    margin-right: 1rem;\r\n}\r\n\r\n.contact-item h4 {\r\n    font-weight: 600;\r\n    